@import "../node_modules/uxcore-kuma/src/variables";

//基于variables->form.less

.kuma-uxform {
    zoom: 1;
    &:after {
        visibility: hidden;
        display: block;
        font-size: 0;
        content: " ";
        clear: both;
        height: 0;
    }
    .kuma-input {
        padding: 5px 10px;
        height: 28px;
        line-height: 28px;
        border: 1px solid @input-border;
        color: @input-color;
        font-size: @font-size-small;
        width: 100%;
    }

    .kuma-input:hover,
    .kuma-textarea:hover,
    .kuma-input-hover {
        border: 1px @input-border solid;
    }

    .kuma-input:focus,
    .kuma-textarea:focus,
    .kuma-input-focus {
        border: 1px @input-border-focus solid;
        color: @input-color;
        outline: none;
    }

    .kuma-textarea {
        line-height: 20px;
        border: 1px @input-border solid;
        padding: 8px 10px;
        overflow: auto;
        font-family: Arial;
        font-size: @font-size-small;
        color: @input-color;
        width:100%;
        resize: vertical;
    }

    .kuma-input[disabled],
    .kuma-textarea[disabled] {
        border: 1px solid @input-border;
        background: @input-bg-disabled;
    }

    .kuma-input:disabled,
    .kuma-textarea:disabled {
        border: 1px solid @input-border;
        background: @input-bg-disabled;
    }


    .kuma-label {
        width: 100px;
        margin-left: 0px;
        padding-top: 5px;
        padding-right:0px;
        text-align: right;
        color: @label-color;
        display:table-cell;
        vertical-align: baseline;
        vertical-align: -webkit-baseline-middle;
        .required {
           color: Red; 
        }
        .label-content {
            display: inline-block;
            line-height: 14px;
        }
    }
    
}

.kuma-uxform-row-title {
    font-size: 14px;
    padding: 15px 0 20px;
    line-height: 14px;
    font-weight: bold;
    clear: both;
    > span {
        border-left: 4px solid #ff9300;
        display: inline-block;
        padding-left: 12px;
    }
}

.kuma-uxform-row {
    min-width: 100%;
    float: left;
    clear: both;
}

.kuma-uxform-field {
    display: table;
    float: left;
    width: 100%;
    padding: 0 5px 15px 0px;
    *zoom: 1;
    .kuma-label {
        padding-top: 10px;
    }
    &:after { 
        clear: both;
        content: '.';
        display: block;
        height: 0;
        visibility: hidden;
    }
    select { 
        background-image: url("data:image/png;base64,");
        background-position: center right -15px;
        background-repeat: no-repeat;
        background-size: 7px 11px;
        box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075);
        border-bottom-color: #c2c2c2;
        border-top-color: #d6d6d6;
        height: 30px;
        width:100%;
    }
    input {
        margin-right: 2px;
        vertical-align: middle;
    }
    .kuma-input {
        color: @input-color;
        vertical-align: middle;
        *vertical-align: -5px;
        height: 36px;
    }
}

.kuma-uxform-field-content {
    font-size: 12px;
    color: @input-color-explain;
    width: 100%;
    padding: 0 0 0 20px;
    .kuma-uxform-tips {
        padding-top: 8px;
        color: #999999;
        .kuma-icon {
            color: #39bffc;
            margin-right: 4px;
        }
    }
    .kuma-uxform-errormsg {
        padding-top: 8px;
        color: #ea5652;
        .kuma-icon {
            margin-right: 4px;
        }
    }
    .kuma-radio-group {
        > span {
            padding: 0px 20px 0px 4px;
        }
    }
    &.view-mode {
        padding-top: 10px;
    }
}

.kuma-button-group-uxform-field {
    padding-left: 120px;
    .kuma-button {
        margin-right: 20px;
   }
}
.kuma-checkbox-group-uxform-field,
.kuma-radio-group-uxform-field {
    .kuma-uxform-field-content {
        padding-top: 10px;
    }
}
.kuma-cascade-date-uxform-field {
    .kuma-calendar-picker {
        width: 200px;
        display: inline-block;
        .kuma-icon-calender {
            top: 9px;
        }
    }
}

.kuma-uxform-split {
    display: inline-block;
    margin: 0 10px;
    vertical-align: middle;
    line-height: 36px;
}

.kuma-select-uxform-field,
.kuma-cascade-select-uxform-field {
    .kuma-select2-selection {
        min-height: 36px;
        .kuma-select2-selection__rendered {
            line-height: 34px;
        }
        .kuma-select2-arrow {
            height: 34px;
        }
    }
}

.kuma-select-uxform-field {
    .kuma-select2 {
        width: 100%;
    }
    .kuma-select2-selection--multiple > ul > li {
        margin-top: 7px;
    }
}

.kuma-cascade-select-uxform-field {
    .kuma-select2 {
        width: 200px;
    }
}

.kuma-grid-uxform-field {
    .kuma-input {
        height: 30px;
    }
}







